import { useState } from 'react';
import axios from 'axios';
import GoogleMap from './GoogleMap';
import { ClaimThumbnail } from './ClaimThumbnail';

const ClaimDetails = ({ foundClaim, deleteClaim, updateClaim, setIsOpen }) => {
  const [mapIsOpen, setMapIsOpen] = useState(false); // true when user clicks to view map
  const [claimAddress, setClaimAddress] = useState({}); // claim address
  const {
    _id,
    company,
    number,
    address,
    exterior,
    interior,
    water,
    fire,
    wind,
    hail,
    description,
    url
  } = foundClaim;
  
  function getAddress() {
    if (mapIsOpen) {
      setMapIsOpen(false);
      return;
    }

    axios
      .get(`${process.env.REACT_APP_GOOGLE_API_URL}`, {
        params: {
          address: address,
          key: process.env.REACT_APP_GOOGLE_API_KEY,
        },
      })
      .then((res) => {
        // console.log(res);
        setClaimAddress({
          center: {
            lat: res.data.results[0].geometry.location.lat,
            lng: res.data.results[0].geometry.location.lng,
          },
        });
        setMapIsOpen(true);
      })
      .catch((err) => {
        alert('This is an invalid address. Please update it.')
        // console.log(err)
      });
  }

  return (
    <div className="modal">
      <button className='close-btn' onClick={() => setIsOpen(false)}></button>
      <div className="claim-details">
        <p><strong>Claim Details</strong></p>
        <p>{`Insurance company: ${company}`}</p>
        <p>{`Claim number: ${number}`}</p>
        <p>{`Claim address: ${address}`}</p>
        <hr />
        <p><strong>Damage Type</strong></p>
        <p>{`Exterior damage: ${exterior}`}</p>
        <p>{`Interior damage: ${interior}`}</p>
        <hr />
        <p><strong>Damage caused by</strong></p>
        <p>{`Water: ${water}`}</p>
        <p>{`Fire: ${fire}`}</p>
        <p>{`Wind: ${wind}`}</p>
        <p>{`Hail: ${hail}`}</p>
        <hr />
        <p><strong>Description</strong></p>
        <p>{description ? description : 'No description provided.'}</p>
        <hr />
        <p><strong>Photos</strong></p>
          {/* {url ? <p><a href={url} target="_blank" rel="noopener noreferrer" alt='image of damage' >See image</a></p> : <p>No photos provided.</p>} */}
          {/* NEED THIS ONE */}
          {/* {url ? <ClaimThumbnail image={url} /> : <p>No photos provided.</p>} */}
          {url 
          ? (url.includes(',') 
            ? url.slice(0, url.length-1).split(',').map((item, i) => <ClaimThumbnail key={i} image={item} />) : <ClaimThumbnail image={url} />)
          : <p>No photos provided.</p>}
        
        <hr />
      </div>
      
      <div className='btn-container'>
        <button className='claim-details-btn' onClick={() => deleteClaim(_id)}>Delete claim</button>
        {/* <button className='claim-details-btn' onClick={() => updateClaim(foundClaim)}>Update claim</button> */}
        <button className='claim-details-btn' onClick={getAddress}>{mapIsOpen ? 'Close map' : 'View map'}</button>
      </div>
      {mapIsOpen && (
        <GoogleMap claimAddress={claimAddress} setMapIsOpen={setMapIsOpen} />
      )}
    </div>
  );
};

export default ClaimDetails;
