import GoogleMapReact from 'google-map-react';

export default function GoogleMap({ claimAddress, setMapIsOpen }) {
  return (
    <div className="map-modal">
      <button className="map-btn" onClick={() => setMapIsOpen(false)}></button>
      <GoogleMapReact
        bootstrapURLKeys={{
          key: process.env.REACT_APP_GOOGLE_API_KEY,
        }}
        center={claimAddress.center}
        zoom={11}
      >
        <div
          className="marker"
          onClick={() => alert("This is the user's location.")}
          lat={claimAddress.center.lat}
          lng={claimAddress.center.lng}
        ></div>
      </GoogleMapReact>
    </div>
  );
}
