import React, {useEffect, useState} from 'react'
import ClaimDetails from './ClaimDetails'
import DamageInfoForm from './DamageInfoForm'

const Claims = () => {
  const [claims, setClaims] = useState([])
  const [isOpen, setIsOpen] = useState(false) // true when viewing a claim
  const [foundClaim, setFoundClaim] = useState(null)
  const [update, setUpdate] = useState(false) // true when found going to update a claim
  const [claimToUpdate, setClaimToUpdate] = useState(null) // id of claim that will be updated
  
  // console.log(claims);
  // here we'll make our request to the database to retrieve all existing claims
  useEffect(() => {
    const fetchClaims = () => {
      fetch(`${process.env.REACT_APP_BACKEND_URL}/getclaims`)
      .then(res => res.json())
      .then(data => {
        // console.log(data);
        setClaims(data);
      })
    }

    fetchClaims();
  }, [claims])

  function deleteClaim(id) {
    // console.log('deleting', id);
    fetch(`${process.env.REACT_APP_BACKEND_URL}/deleteclaim/${id}`, {
      method: 'DELETE'
    })
    // .then(res => res.json())
    // .then(data => console.log('deleted claim', data))
    .catch(err => console.log(err));
    setIsOpen(false)
  }

  function getClaim(id) {
    const claim = claims.find(claim => claim._id === id);
    setFoundClaim(claim);
    setIsOpen(true);
  }
  // console.log(foundClaim.url);

  function updateClaim(claim) {
    // console.log('going to update claim', claim)
    setIsOpen(false);
    setUpdate(true);
    setClaimToUpdate(claim)
  }

  return (
    <div className='main-claims-container'>
    <h3>Active Claims</h3>
      {claims.length > 0 ? (
        <div className='claims-container'>
          {claims.map((claim) => {
            return (
              <div key={claim._id} className='claim'>
                <h4>{`Insurance company: ${claim.company.toUpperCase()}`}</h4>
                <h5 >{`Claim address: ${claim.address}`}</h5>
                <div style={{display: 'flex', justifyContent: 'space-around'}} >
                  <button className='claim-btn' onClick={() => getClaim(claim._id)} >View claim</button>
                </div>
                {isOpen && (
                  <ClaimDetails 
                    foundClaim={foundClaim} 
                    deleteClaim={deleteClaim} 
                    updateClaim={updateClaim}
                    setIsOpen={setIsOpen}
                  />
                )}
              </div>
              )
            })}
        </div> 
        ) : (
          <h4>There are no active claims.</h4>
        )}
        {update && (
        <div className='update-modal'>
          <button className='close-btn' onClick={()=>setUpdate(false)}></button>
          <h3>Update your entry</h3>
          <DamageInfoForm 
            claimToUpdate={claimToUpdate}
            setUpdate={setUpdate}/>
        </div>
        )}
    </div>
  )
}

export default Claims